import React from "react"
import {Link} from "react-router-dom"

import LandingPageDefault from "./LandingPage/LandingPage"

function LandingPage(props){
    
    if(props.user === undefined){
    return(<LandingPageDefault></LandingPageDefault>)
    }
    else if(props.user !== undefined){
        console.log(props.user.Rol)
        if(props.user.Rol === "Supervisor"){
            return (
                <div className="HomeMenuContainer">
                    
                    <Link to="/Requisiciones">
                    <center>
                    <div className="HomeMenuOption">
                        <h2>Requisiciones</h2>
                    </div>
                    </center>
                    </Link>
                    <Link to="/DarAlta">
                    <center>
                    <div className="HomeMenuOption">
                        <h2>Dar alta personal</h2>
                    </div>
                    </center>
                    </Link>
                    
                </div>
            )

        }
    else if(props.user.Rol === "Ingeniero" ){
            return (
                <div className="HomeMenuContainer">
                <Link to="/Requisiciones">
                <div className="HomeMenuOption">
                    <h2>Requisiciones</h2>
                </div>
                </Link>
                <Link to="/DarAlta">
                <div className="HomeMenuOption">
                    <h2>Dar alta personal</h2>
                </div>
                </Link>
            </div>
            )
            
        }
    else if(props.user.Rol === "Admin" ){
        console.log("Here")
            return (
                <div className="HomeMenuContainer">
                    <Link to="/AdminRequisiciones">
                    <div className="HomeMenuOption">
                        <h2>Requisiciones</h2>
                    </div>
                    </Link>
                    <Link to="/DarAlta">
                    <div className="HomeMenuOption">
                        <h2>Dar alta personal</h2>
                    </div>
                    </Link>
                </div>
            )
            
        }
    }
}

export default LandingPage