import React, {useState} from "react"

function ArticulosSolicitados(props){

    const Articulo = () =>{
        var data = {
            Descripcion:"",
            Unidades:"",
            Cantidad:"",
            Comentario:""
        }
    
    
        const [Descripcion, setDescripcion] = useState(null);
        const [Unidades, setUnidades] = useState(null);
        const [Cantidad, setCantidad] = useState(null);
        const [Comentario, setComentario] = useState(null)

        const Feedback = ()=>{
            const [Added, setAdded] = useState(false);
    
            function confirmacion(){
                setAdded(true);
                data.Descripcion = Descripcion;
                data.Unidades = Unidades;
                data.Cantidad = Cantidad;
                data.Comentario = Comentario;
        
                props.obj.Articulos.push(data)
                console.log(props.obj.Articulos)
            }
            if (Added === true) {
                return(
                    <React.Fragment>
                    <button style={{marginBottom:"20px"}}onClick={confirmacion}>Agregar</button>
                    <img alt="Yes" style={{width:"50px",marginBottom:"20px"}} src="https://image.flaticon.com/icons/png/512/3572/3572255.png"></img>
                    </React.Fragment>
                )
            }

            else if(Added === false)   {
                return(
                    <React.Fragment>
                    <button onClick={confirmacion}>Agregar</button>
                    </React.Fragment>
                )
            }
        }
        function handleChangeDescripcion(e){
            setDescripcion(e.target.value);
        }
        function handleChangeUnidades(e){
            setUnidades(e.target.value);
        }
        function handleChangeCantidad(e){
            setCantidad(e.target.value);
        }
        function handleChangeComentario(e){
            setComentario(e.target.value);
        }
        
        return(
            <div className='ArticuloSolicitado'>
            <label>Descripción</label>
            <input onChange={handleChangeDescripcion} placeholder='Ingrese la descripción'></input>
            <label>Unidades</label>
            <input onChange={handleChangeUnidades} placeholder='Ingrese las unidades de medida'></input>
            <label>Cantidad</label>
            <input onChange={handleChangeCantidad} placeholder='Ingrese la cantidad que ocupa'></input>
            <label>Comentario</label>
            <input onChange={handleChangeComentario} placeholder='Ingrese un comentario'></input>
            <Feedback></Feedback>

        </div>
        )
    }





    if(props.Array !== null){
    return(
    <React.Fragment>
    {props.Array.map( (id)=>{
        
        return (
            <Articulo key={id}></Articulo>
        )
        })
    }
    </React.Fragment>
    )
    }else return null

    
    


}

export default ArticulosSolicitados