import React, {useState} from "react"
import Clientes from "./Clientes"
import Footer from "./Footer"


function LandingPageDefault(){

    const Carusel = ()=>{



        var obj = {
            firstImg:{
                small: "https://i.ibb.co/4YXxhvN/480x320.jpg",
                medium: "https://i.ibb.co/P1HYt6v/720x480.jpg",
                large: "https://i.ibb.co/gjJXVtz/95800673-10163499092385511-5478261224481226752-n.jpg"
            },

            secondImg:{
                small: "https://i.ibb.co/M5dtjSy/420x500.jpg",
                medium: "https://i.ibb.co/ZKT26V8/720x900.jpg",
                large: "https://i.ibb.co/y00SQVB/1080x1320.jpg"
            },

            thirdImg:{
                small: "https://i.ibb.co/yhJNWn3/420x500-Almacen.jpg",
                medium: "https://i.ibb.co/XFd8bnD/720x1000.jpg",
                large: "https://i.ibb.co/GF5t1bc/1080x1200.jpg"
            },
            fourthImg:{
                small: "https://i.ibb.co/DRmwqnr/420x-Codigo.png",
                medium: "https://i.ibb.co/hR8P9fG/780x440.png",
                large: "https://i.ibb.co/Fg3P9Yd/1080x400.png"
            },
            fifthImg:{
                small: "https://i.ibb.co/J2zfR3B/cripsin-420.jpg",
                medium: "https://i.ibb.co/r2sbYKV/Crispin760.jpg",
                large: "https://i.ibb.co/BGPBQBp/crispin1080.jpg"
            },
            sixthImg:{
                small: "https://i.ibb.co/fX7L4P5/phone420.jpg",
                medium: "https://i.ibb.co/hdxYF21/phone720.jpg",
                large: "https://i.ibb.co/VDGwbRH/phone1080.jpg"
            },
            seventhImg:{
                small:"https://i.ibb.co/ynx1hQh/boda480.jpg",
                medium:"https://i.ibb.co/tJbM3zv/boda720.jpg",
                large:"https://i.ibb.co/j5kWcyW/boda1080.jpg"
            }

        }
        const [Src , setSrc] =  useState(obj.firstImg)
        const [Txt, setTxt] = useState(0)

        var text = [
            "La Familia Es El Nucleo De Toda Empresa.", "Buscando la mejora continua en obra", "Y en nuestra logistica interna", 
            "A travez de horas incontables de esfuerzo e investigación", "Creamos una empresa, organizada y reconocida",
            "La cual cuenta con su propio software para facilitar la logistica", "Todo esto fue creado por tu esfuerzo a lo largo de los años. Y hoy cumples otro año de dar tu mejor esfuerzo ¡Feliz Cumpleaños Papa!"

        ]


        function handleImgSrc(param,Txt){
                console.log("Txt ↓")
                console.log(Txt)

                if(param === "Plus"){
                    console.log("This ran")
                    if(Txt === 0){setSrc(obj.secondImg)}
                    else if(Txt === 1){setSrc(obj.thirdImg)}
                    else if(Txt === 2){setSrc(obj.fourthImg)}
                    else if(Txt === 3){setSrc(obj.fifthImg)}
                    else if(Txt === 4){setSrc(obj.sixthImg)}
                    else if(Txt === 5){setSrc(obj.seventhImg)}
                }else{
                    console.log("That ran")
                    if(Txt === 0){setSrc(obj.firstImg)}
                    else if(Txt === 1){setSrc(obj.firstImg)}
                    else if(Txt === 2){setSrc(obj.secondImg)}
                    else if(Txt === 3){setSrc(obj.thirdImg)}
                    else if(Txt === 4){setSrc(obj.fourthImg)}
                    else if(Txt === 5){setSrc(obj.fifthImg)}
                    else if(Txt === 6){setSrc(obj.sixthImg)}
                }

        }


        const Controls = ()=>{
            return (
                <div className="Control-Container">
                    <button onClick={()=>{if(Txt>0){setTxt(Txt-1); handleImgSrc("Minus",Txt)}}}>←</button>
                    <button onClick={()=>{if(Txt<6){setTxt(Txt+1); handleImgSrc("Plus",Txt)}}}>→</button>
                </div>
            )

        }




        return(
            <div className="Carusel-Container">
            <picture>
                <source
                    media="(min-width: 960px)"
                    srcset={Src.large}
                />
                <source
                    media="(min-width: 600px)"
                    srcset={Src.medium}
                />
                <source
                srcset={Src.small}
                />
                <img class="caruser-img" src="https://i.ibb.co/gjJXVtz/95800673-10163499092385511-5478261224481226752-n.jpg" alt="image" ></img>
            </picture>
                <Controls></Controls>       
                <div className="Carusel-Text">
                    <h1>{text[Txt]}</h1>
                </div>
            </div>
        )


    }

    return(
        <main>
            <section>
                <Carusel></Carusel>
            </section>
            <section>
                <Clientes></Clientes>
            </section>
            <section>
                <Footer></Footer>
            </section>
            <section style={{marginBottom:"100px"}}></section>
        </main>
    )


}

export default LandingPageDefault