function HistorialApp(props){
    return(
        <div id="HistorialApp">
                            {props.aprobacionesDisplay.map((vale)=>{
                                var Articulos = JSON.parse(vale.Articulos);
                                        return(
                                            <div className="Vale-Requisicion">
                                                <div className="Vale-Requisicion-Header">
                                                        <div>
                                                            <h3>Usuario</h3>
                                                            <p>{vale.Usuario}</p>
                                                        </div>
                                                        <div>
                                                            <h3>Uso</h3>
                                                            <p>{vale.Uso}</p>
                                                        </div>
                                                        <div>
                                                            <h3>Folio</h3>
                                                            <p>{vale.Folio}</p>
                                                        </div>
                                                        <div>
                                                            <h3>Fecha</h3>
                                                            <p>{vale.Fecha}</p>
                                                        </div>
                                                </div>
                                                <div className="Vale-Requisicione-Articulos-Header">
                                                    
                                                        <h3>Descripción</h3>
                                                        <h3>Unidades</h3>
                                                        <h3>Cantidad</h3>
                                                        <h3>Comentario</h3>
                                                    
                                                </div>
                                                <div>
                                                    {Articulos.map((Articulo)=>{
                                                        return(
                                                            <div class="Vale-Requisicion-Articulo">
                                                                <p>{Articulo.Descripcion}</p>
                                                                <p>{Articulo.Unidades}</p>
                                                                <p>{Articulo.Cantidad}</p>
                                                                <p>{Articulo.Comentario}</p>
                                                            </div>
                                                        )
                                                    })}
                                                </div>
                                                <div>
                                                    <h3>Si desea ver el documento, dando click abajo</h3>
                                                    <a href={vale.GeneradoURL}><button style={{ width: "-webkit-fill-available",fontSize: "xx-large"}} >Ver Documento</button></a>
                                                </div>
                                            </div>
                                        )
                            })}
                        </div>
    )

}

export default HistorialApp