import React, {useState, useEffect} from "react"
import HistorialApp from "./NestedCoomponents/HistorialApp";
import {Link} from "react-router-dom"

import AprobacionesApp from "./NestedCoomponents/AprobacionApp";


function AdminRequisiciones(props){

    const [app, setapp] = useState(undefined)
    const [data, setData] = useState(undefined)

    useEffect(()=>{
        props.SetOptions("AdminRequisicion");
        props.postArray.splice(0, props.postArray.lenght);


        var url="https://script.google.com/macros/s/AKfycbzHF7M6mRxaXFg1KhF1DwAAYk0oshGa4MwKJuiIVFQnNmEzMosiBRQYuSh27tUfKrde/exec";

        fetch(url)
            .then(response => {
            return response.json()
            }).then(data =>{
                

               setData(data);

            })
                

    }, [])

    var aprobacionesDisplay = [];
    var historialDisplay = []

    if (data !== undefined){
        var x = 0;
    data.forEach((vale) =>{
        if(x<50){
        aprobacionesDisplay.push(vale)
        }
        if(x<200){
        historialDisplay.push(vale)
        }
        x++
    })
    }
    
  

    if(props.user === undefined){
        alert("Necesita iniciar sesion para usar esta pagina")
        return null
    }else{
        if(props.user.Rol !== "Admin"){
            alert("No tienes los permisos necesarios para ver este contenido")
            return null
        }else{

            const SubApp = (props) =>{
                if (props.app === undefined) return null
                else if(props.app === "Historial"){
                    return (
                        <HistorialApp aprobacionesDisplay={historialDisplay}></HistorialApp>
                    )
                }else if(props.app === "Aprobaciones"){
                    return( 
                        <AprobacionesApp aprobacionesDisplay={aprobacionesDisplay}></AprobacionesApp>
                    )
                }

            }

            function SetAppHandler(param){
                setapp(param)
            }


            if(app === undefined){
            return(
                <main id="AdminRequisiciónBody">
                    <section id="Sidepanel">
                        <div onClick={()=>{SetAppHandler("Aprobaciones")}} className="HomeMenuOption">
                            <h2>Aprobaciónes</h2>
                        </div>
                        <Link to="/Requisiciones">
                        <div className="HomeMenuOption">
                            <h2>Generar</h2>
                        </div>
                        </Link>
                        <div onClick={()=>{SetAppHandler("Historial")}} className="HomeMenuOption">
                            <h2>Historial</h2>
                        </div>
                    </section>
                    <section id="Content">
                        <SubApp app={app}></SubApp>
                    </section>
                    <div style={{marginBottom:"100px"}}></div>

                </main>
            )
            }else{
                return(
                <main>
                    <button onClick={()=> setapp(undefined)}>Regregar</button>
                    <section id="Content">
                        <SubApp app={app}></SubApp>
                    </section>
                    <div style={{marginBottom:"100px"}}></div>
                </main>
                )
            }
        }
    }

}

export default AdminRequisiciones