import React, {useEffect, useState} from "react";
import ArticulosSolicitados from "./NestedComponent/ArticulosSolicitados";

function Requisicion(props){

    useEffect(() => {
        props.SetOptions("Requisiciones");
        props.postArray.splice(0, props.postArray.lenght);  
        console.log(props.postArray)
        console.log("useEffect Ran")
    }, [])

    const [Uso, setUso] = useState(null);
    const [Folio, setFolio] = useState(null);
    const [Cuantos, setCuantos] = useState(0);
    const [Continuar, setContinuar] = useState(true)
    const [stateArray, setstateArray] = useState(null)

    var Array = []

    var obj = {
        Articulos : []
    }

    const Inicio = ()=>{

        if(Continuar === true) return <button onClick={()=> GenerarRequisicion(Cuantos)}>Continuar</button>
        else if(Continuar === false) return null 
    }
    const Guardar = ()=>{
        if(Continuar === false) return <button style={{marginBottom:"120px"}} onClick={GuardarRequisicion}>Guardar</button>
        else if (Continuar === true) return null
    }
    const Error = ()=>{
        if(Cuantos<1 || Cuantos>8){
            return <p>Necesitas tener por lo menos 1 articulo o maximo 8</p>
        }else return null
    }

    function GenerarRequisicion(num){
        if(num<1 || num>8){
            alert("Necesitas tener por lo menos 1 articulo o maximo 8");
        }else if(Uso === null || Folio === null){
            alert("Te hace falta el Uso o el Folio")
        }else{

        for(var x=0;x<num;x++){
            Array.push(x)
        }
        setstateArray(Array)
        props.postArray.push(props.user.NombeDeUsuario, Uso, Folio)
        setContinuar(false)

        }

        
    }

    function GuardarRequisicion(){

        props.postArray.push(obj.Articulos);
        alert("Los información ha sido guardada, ahora puede enviarla");
        console.log(props.postArray)
        
    }
    function HandleCuantosChange(e){
        setCuantos(e.target.value)
        console.log(Cuantos)
    }
    function HandleChangeFolio(e){
        setFolio(e.target.value)
        console.log(Folio)
    }
    function HandleChangeUso(e){
        setUso(e.target.value)
        console.log(Uso)
    }
    
    var Permisions = ["Admin", "Ingeniero", "Supervisor"];

    if (props.user !== undefined){
        if(Permisions.indexOf(props.user.Rol) !== -1 ){
            return(
                <div className="RequisicionForm">
                    <label>Uso</label>
                    <input onChange={HandleChangeUso} placeholder="¿Cual es su uso?"></input>
                    <label>Folio</label>
                    <input onChange={HandleChangeFolio} placeholder="¿Cual es el folio?"></input>
                    <label>¿Cuantos articulos vas a solicitar?</label>
                    <input onChange={HandleCuantosChange} type="number" min="1" max="8" ></input>
                    <Error></Error>
                    <Inicio></Inicio>
                    <ArticulosSolicitados Array={stateArray} obj={obj} postArray={props.postArray}></ArticulosSolicitados>
                    <Guardar></Guardar>
                </div>
            )

        }else if(Permisions.indexOf(props.user.Rol) === -1 ){

            alert("No cuentas con los permisos necesarios")

            return null

        }
    }
    else if(props.user === undefined){
        alert("Necesitas iniciar sesion para continuar")
        return null
    }

}

export default Requisicion