

function Options(props){

    function EnviarAsistencia(){
        const url = "https://script.google.com/macros/s/AKfycbxQsyfKvDF8k-h3pkyfGlxWpMH99vixdV9JVRO9-3hhNrLC8HhEacj55BUugFd-mdkF/exec"
        var date = new Date();

        if(date.getHours()>12){
            alert("No se puede reportar Asistencia despues de Medio día")
        }else if(date.getHours()<= 12){

        fetch(url, {
            method:"POST",
            mode: "no-cors",
            cache:"no-cache",
            headers: {"Content-Type":"aplication/json"},
            redirect:"follow",
            body: JSON.stringify({"Array": props.postArray, "user":props.user})
        })
    }
        alert("La asistencia ha sido enviada, revisa tu correo para confirmar");
    }

    function EnviarRequisicion(){
        const url = "https://script.google.com/macros/s/AKfycbyr26IShAGAcy4bREPxQUTlAm6wH3VvmAXOo5gjNEUxAkjW-C9NEKyurSSEHTEGjbW7/exec";

        fetch(url, {
            method:"POST",
            mode: "no-cors",
            cache:"no-cache",
            headers: {"Content-Type":"aplication/json"},
            redirect:"follow",
            body: JSON.stringify({"Array": props.postArray, "Command":"GenerarRequisiación"})
        }).then((response)=>{

            alert("Tu requisición ha sido enviada")
        })

    }
  
    if(props.user !== undefined){
    if(props.OpenModal === true){


        

        if(props.DisplayOptions === "Asistencia"){
            if(props.user.Asistencia === true){
                return (
                <div id="OptionsModal">
                   <center><button style={{fontSize:"xx-large", margin:"20px"}} onClick={EnviarAsistencia}>Enviar</button></center>
                </div>
            )}if(props.user.Asistencia === false) {
               
                return <h1>This shouldn't happen</h1>
            }
        } else if(props.DisplayOptions === "Requisiciones"){
            
            return(
            <div id="OptionsModal">
                <center><button style={{fontSize:"xx-large", margin:"20px"}} onClick={EnviarRequisicion}>Enviar</button></center>
            </div>
            )

        }
        else {
            alert("No existen opciones para esta pagina");
            return null
        }   
    



    }if(props.OpenModal === false){
      
        return null
    }
    }
    else if(props.user === undefined){
        if(props.OpenModal === true) {alert("Necestias iniciar sesion"); return null;}
        if(props.OpenModal === false) return null
    }
}




export default Options